/* eslint-disable jsx-a11y/alt-text */
import { useState, useEffect } from 'react' 
import axios from 'axios'

import '../styles/Testimonials.css'
import { API_URL, BUCKET } from '../utils/Variables'
import Loader from './Loader'


const Testimonials = () => {
    const [testimonials, setTestimonials] = useState([])
    const [displayedTestimonial, setDisplayedTestimonial] = useState(0)
    const [error, setError] = useState()
    const [loader, setLoader] = useState(false)

    const scrollTestimonial = direction => {
        if (direction === 'up') {
            if (displayedTestimonial === (testimonials.length - 1)) {
                setDisplayedTestimonial(0)
            } else {
                setDisplayedTestimonial(displayedTestimonial + 1)
            }
        } else {
            if (displayedTestimonial === 0) {
                setDisplayedTestimonial(testimonials.length - 1)
            } else {
                setDisplayedTestimonial(displayedTestimonial - 1)
            }
        }
    }


    useEffect(() => {
        // grab the testimonials from the server
        setLoader(true)
        axios.get(`${API_URL}/testimonials`).then(response => {
            setTestimonials(response.data)
            setLoader(false)
        }).catch(error => {
            setError(error.message)
            setLoader(false)
        })
    }, [])


    return(
        <div className="TestimonialsBackgroundContainer">
            <h1 className="PageTitle">Testimonials</h1>
            <div className="PageContainer">           

                <div className='TestimonialsContainer'>
                    <img 
                    className="ScrollButton" 
                    src={`${BUCKET}/assets/left-arrow.png`}
                    alt='scroll left'
                    onClick={() => scrollTestimonial('up')}
                    />

                    { loader && <Loader loaderType="Mini" /> }

                    { 
                        testimonials[0] ?
                            <div className="TestimonialStatementContainer">
                                <p className="TestimonialStatement">"{ testimonials[displayedTestimonial].statement }"</p>
                            </div>
                        : <h2>{ error }</h2>
                        
                    }

                    <img className="ScrollButton"
                    src={`${BUCKET}/assets/right-arrow.svg`} 
                    alt='scroll right'
                    id="scrollRight"
                    onClick={() => scrollTestimonial('down')}
                    />
                </div>
                
                { testimonials[0] && <p className="TestimonialAuthor">- { testimonials[displayedTestimonial].author }</p> }

            </div>
        </div>
    )
}


export default Testimonials