import ItemThumbnail from '../components/ItemThumbnail'

const Shop = props => {
    let filteredProducts

    if (props.slug === 'all') {
        filteredProducts = props.products
    } else {
        filteredProducts = props.products.filter(product => product.collection === props.slug)
    }

    return(
        <div>
            <h1 className="PageTitle">Products</h1>

            <div className="CollectionsContainer PageContainer">
                { filteredProducts && filteredProducts.map(product => <ItemThumbnail key={ product._id } item={ product } /> )}
            </div>
        </div>
    )
}

export default Shop