import { Link } from 'react-router-dom'

import { BUCKET } from '../utils/Variables'
import ItemThumbnail from '../components/ItemThumbnail'

import '../styles/Collections.css'
import '../styles/ItemThumbnail.css'

const Collections = props => {
    // build out collections from products dynamically
		let collections = []  
		
    for (let i = 0; i < props.products.length; i++) {
				let matched
				
        for (let j = 0; j < collections.length; j++) {
            if (props.products[i].collection === collections[j].collection) {
                matched = true
            }
				}
				
        if (!matched) {
            collections.push(props.products[i])
        }
    }
		
    return(
        <div>
            <h1 className="PageTitle">Collections</h1>
            <div className="PageContainer CollectionsContainer">
                <div className="ItemThumbnailWrapper">
                    <Link
                        to='/collections/all'
                        key='all-products'
                        className='ItemThumbnail'
                    >
                        <div className="ItemThumbnailImageContainer">
                            <img className="ItemThumbnailImage" src={`${BUCKET}/product_images/cordyceps/1.jpeg`} alt="array of products" />
                        </div>
                        <h1 className="ItemThumbnailName">all products</h1>
                    </Link>                    
                </div>

                
                { collections.map(collectionItem => <ItemThumbnail key={ collectionItem._id } item={ collectionItem } type='collection' />) }
            </div>
        </div>
    )
}

export default Collections