import { Link } from 'react-router-dom'

import { BUCKET } from '../utils/Variables'
import '../styles/ItemThumbnail.css'

const ItemThumbnail = props => {
    if (props.type === 'collection') {
        return(
            <div className="ItemThumbnailWrapper">
                <Link
                    to={ `/collections/${ props.item.collection }`}
                    key={ props.item._id }
                    className='ItemThumbnail'
                >
                    <div className="ItemThumbnailImageContainer">
                        <img className="ItemThumbnailImage" src={`${BUCKET}/product_images/${ props.item.slug}/${props.item.images[0]}`} alt={props.item.name} />
                    </div>
                    <h1 className="ItemThumbnailName">{ props.item.collection }<span>s</span></h1>
                </Link>                    
            </div>
        )        
    } else {
        return(
            <div className="ItemThumbnailWrapper">
                <Link 
                    to={`/shop/${props.item.slug}`}
                    key={props.item.slug}
                    className='ItemThumbnail'
                >
                    <div className="ItemThumbnailImageContainer">
                        <img className="ItemThumbnailImage" src={`${BUCKET}/product_images/${props.item.slug}/${props.item.images[0]}`} alt='product' />
                    </div>
                    <h1 className="ItemThumbnailName">{props.item.name}</h1>
                    <p className="ItemThumbnailPrice">${props.item.price}</p>
                    <p className="ItemThumbnailDescription">{props.item.description}</p>
                </Link>                    
            </div>
        )
    }

}

export default ItemThumbnail